import React, { Component  } from "react";
import Fade from "react-reveal";


let id = 0;
class Portfolio extends Component {
  
  render() {
  
    if (!this.props.data) return null;

    const proyectos = this.props.data.proyectos.map(function (proyectos) {
      let proyectosimagen = "imagenes/portfolio/" + proyectos.imagen;
      let url = proyectos.url;

      return (
        <div key={id++} className="columnas portfolio-item">
             <div class="container">
        <div class="card">
            <div class="face face1">
                <div class="content">
                    <div class="icon">
                    <img class="fa fa-linkedin-square" aria-hidden="true"  src={proyectosimagen} Alt=""/>
                 </div>
                </div>
            </div>
            <div class="face face2">
                <div class="content">
                    <h3>
                        <a href={url}>{proyectos.titulo}</a>
                    </h3>
                    <p>{proyectos.category}</p>
                  
                </div>
            </div>
        </div>
   </div>
   </div>



      );
    });

    return (
      <section id="portfolio">
        <Fade left duration={1000} distance="40px">
          <div className="row">
            <div className="twelve columnas collapsed">
              <h1>Mira mis trabajos</h1>

              <div
                id="portfolio-wrapper"
                className="bgrid-quarters s-bgrid-thirds cf"
              >
                {proyectos}
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default Portfolio;
